<template>
  <div class="toast" v-show="is_show">
    <h2>{{ message }}</h2>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      message: "",
      is_show: false,
      //控制定时器
      time: null
    }
  },
  // 插件中的函数
  methods: {
    show(message, delay) {
      clearTimeout(this.time)
      this.is_show = true
      this.message = message
      this.time = setTimeout(() => {
        this.is_show = false
        this.message = ""
      }, delay)
    }
  }
}
</script>

<style scoped>
.toast {
  position: fixed;
  left: 50%;
  top: 50%;
  width: 69.3333vw;
  height: 13.3333vw;
  line-height: 13.3333vw;
  text-align: center;
  transform: translate(-50%, -50%);
  z-index: 9;
  background: rgba(0, 0, 0, 0.7);
}
.toast h2 {
  font-size: 4.8vw;
  font-weight: normal;
  color: #fff;
}
</style>