import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartList: [],
  },
  // mutations我们建议只单独存在一个功能所以可以把多余的功能写在getters里面
  mutations: mutations,
  getters: getters,
  actions: actions,
  modules: {}
})
