export default {
    // mutations里面做的事情尽可能单一
    addcounter(state, payload) {
        payload.count++
    },
    addPlus(state, payload) {
        payload.cheked = true
        state.cartList.push(payload)
    },
    addGoods(state, payload) {
        if(payload.count >= 10) {
            return false
        }
        payload.count += 1
    },
    reduceGoods(state,payload) {
        if(payload.count <= 1) {
            return false            
        }
        payload.count -= 1
    },
    // 清除商品
    clears(state) {
     if(state.cartList.length == 0) {
        return 
     }else {
        state.cartList = []
     }
    }
}