// 引入toast组件
import Toast from '../toast/toast'
let newToast = {}
newToast.install = function (Vue) {
  // 创建组件构造器
  let createTost = Vue.extend(Toast)

  // 根据组件构造器创建组件对象
  let toast = new createTost()

  // 将组件对象手动挂载到某一个元素上
  toast.$mount(document.createElement('div'))

  // 将toast添加到body中 toast.$el对应的就是div
  document.body.appendChild(toast.$el)

  // 在Vue的原型上添加toast
  Vue.prototype.$toast = toast
}
export default newToast