export default {
  addCart(context, payload) {
    return new Promise((resolve, reject) => {
      // 查找之前商品是否有该商品的id,如果该商品的id相同则把这个对象赋值给commodity
      let commodity = context.state.cartList.find(item => item.iid === payload.iid)
      // console.log(commodity)
      // 第一次里面没有值,如果里面有值则当前商品的数量等于一如果有就把当前商品加一
      if (commodity) {
        context.commit('addcounter', commodity)
        resolve('当前商品数量加+1')
      } else {
        // 如果没有值,则给提交过来的对象添加一个count属性
        payload.count = 1
        // 如果没有值那么就把商品对象添加到购物车
        context.commit('addPlus', payload)
        resolve('加入购物车成功 ✔')
      }
    })
  },
  clear(context) {
    // 清空商品
    context.commit('clears')
  }
}