<template>
  <div class="tab-item" @click="item_click">
    <!-- 如果要做属性操作建议包裹一个div标签因为组件的标签要覆盖插槽从而导致插槽不起效果 -->
    <!-- 如果不为true就不展示icon -->
    <div v-if="!isActive"><slot name="icon"></slot></div>
    <!-- 活跃时要展示的图片 -->
    <div v-else><slot name="icon_active"></slot></div>
    <!-- 如果isActive为true就显示活跃时的文字 -->
    <div :style="style_Active"><slot name="text"></slot></div>
  </div>
</template>

<script>
export default {
  data() {
    return {}
  },
  //   接收父组件传递过来的值(mainTabBar)
  props: {
    link: String,
    //如果没有传递颜色值就使用默认的颜色
    active_color: {
      type: String,
      default: "#1296db"
    }
  },
  computed: {
    isActive() {
      //动态修改isActive的值  如果当前活跃路由包含props里面的路径的话就返回true否则为false
      //   return this.$route.path.indexOf(this.link) !== -1
      return this.$route.path == this.link
    },
    style_Active() {
      // 如果isActive为活跃状态(true)就添加颜色否则返回空对象
      // if (this.isActive) {
      //   return { color: this.active_color }
      // } else {
      //   return 
      // }
      return this.isActive ? { color: this.active_color } : {}
    }
  },
  methods: {
    item_click() {
      //   动态传递的路劲   点击item获取props里面的值
      this.$router.replace(this.link).catch(err => err)
    }
  }
}
</script>

<style scoped>
.tab .tab-item {
  flex: 1;
  text-align: center;
  height: 13.0667vw;
  font-size: 3.4667vw;
  color: #333;
  cursor: pointer;
}
.tab .tab-item img {
  width: 6.4vw;
  height: 6.4vw;
  margin-top: 0.8vw;
  vertical-align: middle;
}
.tab section {
  margin-top: 0.5333vw;
}
/* .active {
  color: #1296db;
} */
</style>